import React from 'react';
import { Link, StaticQuery, graphql } from 'gatsby';
// import Img from 'gatsby-image';
import PropTypes from 'prop-types';

const Header = () => (
  <StaticQuery
    query={graphql`
      query {
        fileName: file(
          sourceInstanceName: { eq: "images" }
          name: { eq: "logo" }
        ) {
          childImageSharp {
            fixed(width: 375, quality: 100) {
              ...GatsbyImageSharpFixed_withWebp
            }
          }
        }
      }
    `}
    render={({
      fileName: {
        childImageSharp: { fixed },
      },
    }) => (
      <div className="w-full">
        <div className="flex flex-wrap flex-row items-start md:items-center justify-between bg-green-300">
          <div
            className="w-8/12 md:w-6/12 lg:w-4/12 bg-white"
            style={{ lineHeight: 0 }}
          >
            <Link className="m:pl-4" to="/">
              {/* <Img
                alt="Logo for Vermont Center on Behavior and Health"
                fixed={fixed}
                backgroundColor="white"
                loading="eager"
                durationFadeIn="250ms"
              /> */}
              <img
                alt="Logo for Vermont Center on Behavior and Health"
                width="375px"
                src="/logo.jpg"
                className="mx-auto"
              />
            </Link>
          </div>
        </div>
      </div>
    )}
  />
);

Header.propTypes = {
  className: PropTypes.string,
};

export default Header;
